.cursor {
  // position: fixed;
  // top: 0;
  // left: 0;
  // display: block;
  // pointer-events: none;
  // z-index: 1000;
  display: none;

  width: 5rem;
  aspect-ratio: 1;

  & path {
    fill: #f4e600;
    stroke: #020203;
    stroke-width: 22.4366;
    stroke-miterlimit: 10;
  }

  @media (any-pointer: fine) {
    position: fixed;
    top: 0;
    left: 0;
    display: block;
    pointer-events: none;
    z-index: 1000;
  }
}
